	/*button normal*/
.btn {
  position: relative;
  padding: 12.5px 14px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
  /*&:focus {
  	box-shadow: 0 0 0 4px $pos-color;
  }*/
}
.btn.btn-all {
    width: 100%;
}
.btn.btn-cursor-default {
    cursor: default;
}
.btn.btn-focus:focus {
    background-color: rgba(225, 225, 225, 0.54);
}
.btn.btn-no-focus:focus {
    -webkit-box-shadow: 0 0 0 0 transparent;
            box-shadow: 0 0 0 0 transparent;
}
.btn.btn-no-pad {
    padding: 0 0;
}
.btn.btn-upp {
    text-transform: uppercase;
}
.btn.btn-radius {
    border-radius: 45px;
}
.btn.btn-div {
    padding: 10px 15px;
    height: 45px;
    line-height: 45px;
}
.btn.btn-circle {
    width: 45px;
    height: 45px;
    padding: 0 0;
    border-radius: 100%;
    text-align: center;
}
.btn.btn-main-color {
    background-color: #bd081c;
    color: #fff;
}
.btn.btn-main2-color {
    background-color: transparent;
    color: rgba(0, 0, 0, 0.54);
}
.btn.btn-main2-color:hover {
      -webkit-transition: 0.3s;
      transition: 0.3s;
      background-color: rgba(225, 225, 225, 0.54);
      color: rgba(0, 0, 0, 0.84);
}
.btn.btn-main3-color {
    background-color: #529ecc;
    color: #fff;
}
.btn.btn-main4-color {
    background-color: #fff;
    color: #bd081c;
}
.btn.btn-main5-color {
    background-color: transparent;
    color: #529ecc;
}
.btn.btn-sekunder-color {
    background-color: #fff;
    color: rgba(0, 0, 0, 0.54);
    border: 1px rgba(0, 0, 0, 0.1) solid;
}
.btn.btn-sekunder-color:hover {
      -webkit-transition: 0.3s;
      transition: 0.3s;
      color: rgba(0, 0, 0, 0.84);
      border: 1px rgba(0, 0, 0, 0.54) solid;
}
.btn.btn-sekunder-color.btn-no-border {
      border: 0 #fff solid;
}
.btn.btn-black-color {
    background-color: rgba(0, 0, 0, 0.54);
    color: #fff;
}
.btn.btn-black-color:hover {
      -webkit-transition: 0.3s;
      transition: 0.3s;
      background-color: rgba(0, 0, 0, 0.84);
}
.btn.btn-grey-color {
    background-color: rgba(0, 0, 0, 0.54);
    color: #fff;
}
.btn.btn-grey2-color {
    background-color: rgba(225, 225, 225, 0.34);
    color: rgba(0, 0, 0, 0.54);
}
.btn.btn-grey2-color:hover {
      -webkit-transition: 0.3s;
      transition: 0.3s;
      background-color: rgba(225, 225, 225, 0.54);
      color: rgba(0, 0, 0, 0.84);
}
.btn.btn-grey3-color {
    background-color: #fff;
    color: rgba(0, 0, 0, 0.54);
}
.btn.btn-grey3-color:hover {
      -webkit-transition: 0.3s;
      transition: 0.3s;
      background-color: #f2f2f2;
      color: rgba(0, 0, 0, 0.84);
}
.btn.btn-grey4-color {
    background-color: rgba(225, 225, 225, 0);
